          "artistName": "Gorillaz",
          "contentRating": "explicit",
          "editorialNotes": {
            "standard": "Genre-mashing floor-fillers (Dare), wistful, synth-driven ballads (On Melancholy Hill) candy-coated surrealist rap (Superfast Jellyfish): theres hardly a patch of musical terrain that isnt covered in this punchy edit of the cartoon quartets decade-long recording career. Nestled next to each otherand packaged with Daley hook-up Doncamatictheres detectable growth, and boundless invention, across Jamie Hewlett and Damon Albarns three records. But theres surprising cohesion too and it all amounts to a peerless collection of shape-shifting, collaborative pop.",
            "short": "A decade of shape-shifting pop supremacy."
          },
          "isComplete": true
