          "editorialNotes": {
            "standard": "With its sultry, throbbing bassline, genre-defying melodic turns and surprising guests, Stylo arguably represents Gorillaz' entire sonic approach in microcosm. But while this hypnotic blast of future-soulpropelled by guest vocals from Mos Def and Bobby Womackis an undoubted high point, its by no means the only rich pleasure on this epic, dizzyingly collaborative third album. Inspired by experimentation with opera and bolstered by more fantasy cameos (including Lou Reed), its the virtual bands boldest statement yet.",
            "short": "Hewlett and Albarn marshal a starry, high-concept opera."
          },
          "isComplete": true
