      "1530812327": {
        "id": "1530812327",
        "type": "albums",
        "attributes": {
          "copyright": "Under exclusive licence to Parlophone Records Limited.,  2020 Gorillaz Partnership",
          "genreNames": [
            "Alternative",
            "Music"
          ],
          "releaseDate": "2020-10-23",
          "isMasteredForItunes": true,
          "upc": "190295143152",
          "artwork": {
            "width": 4000,
            "url": "https://is1-ssl.mzstatic.com/image/thumb/Music125/v4/01/d6/6e/01d66e09-0bcc-c560-469b-f6f3af6124b6/190295143152.jpg/{w}x{h}bb.jpg",
            "height": 4000,
            "textColor3": "d0e1c1",
            "textColor2": "f8cce8",
            "textColor4": "cbbad1",
            "textColor1": "fffdd4",
            "bgColor": "177178",
            "hasP3": false
          },
          "url": "https://music.apple.com/us/album/song-machine-season-one-strange-timez-deluxe/1530812327",
          "playParams": {
            "id": "1530812327",
            "kind": "album"
          },
          "recordLabel": "Parlophone UK",
          "isCompilation": false,
          "trackCount": 17,
          "isPrerelease": false,
          "audioTraits": [
            "lossless",
            "lossy-stereo"
          ],
          "isSingle": false,
          "name": "Song Machine, Season One: Strange Timez (Deluxe)",
          "contentRating": "explicit",
          "artistName": "Gorillaz",
          "editorialNotes": {
            "standard": "Gorillaz began making their seventh studio album with the specific intention of not making an album. Instead, the Song Machine project was conceived as a series of monthly stand-alone singles that would be created in the moment, without the constraints or concepts that come with shaping an album. Thats the definition of contemporary, isnt it? Remi Kabaka Jr.producer, drummer, percussionist, and Gorillazs third member alongside Damon Albarn, and Jamie Hewletttells Apple Music. Its more interesting working in this episodic format because its easier to respond to a moment than trying to remember your response to the moment. You are a bulletina responder in the moment, not a reporter of the moment.<br />\nSongs were forged in intensive bursts, as quickly as ideas and collaborators could be brought together. The series launched with Momentary Bliss, a linkup with the insurrectionary voices of slowthai and Slaves, in January 2020 before lockdown changed the cadence of releases to every few weeks. Nevertheless, work continued across all available forms of communication and the music kept coming. I dont know if [lockdown] changed the process of making songs, I think it changed the subject matter, says Kabaka. You like an artist, you play them a track whether its online or IRL, if they like it, youll work together. Technology is just a music delivery system; it hasnt radically changed the creative process for Gorillaz. If you can get in touch with someone and you have something to write about, then boom! You just go from there. The list of collaborators is as rich and as stellar as its ever been on a Gorillaz project, with Elton John, The Cures Robert Smith, Georgia, 6LACK, and Malian singer-songwriter Fatoumata Diawara all drawn into the bands orbit. Song Machine is a universe of sound, and I think there is more university of sound than there ever was, says Kabaka. In a way, Song Machine as a whole has become more cellular.<br />\nGorillazs magic is to fuse those various cells together into a new, cohesive whole. Collaborators were chosen for the way they could inspire or evolve a song as much as how they might fit ideas that were already brewing. The music can loudly choose the artist, <i>or</i> the artists can choose the idea you hadnt actually thought of, he says. You have to have multiple ideas and options; you cant be scared of losing an idea. Thats part of being agile and in the momentyou have to go in with as little preconception as possible, and as much possibility as you can. You have to respond to the needs of the artisttheyre the ones with the master plan. Surprise is why we invited them. You have to hope they can surprise you. The results are still unmistakably Gorillaz: off-kilter pop thats playful, melancholy, worldly, and tethered to sharp melodies. <br />\nIt eventually became clear that there was a very good album to be pieced together from these free-standing songs. And its one that reflects the turbulence and trauma of 2020. On The Valley of the Pagans, Beck sends strutting dispatches from a land of the permanent sun/Where the flowers are melted and the future is fun. ScHoolboy Q bounds across the spongy funk of Pac-Man, asking, How can I trust truth? And, with customary edginess, The Cures Robert Smith alludes to our surgical glove world in the title tracka song that manages to be both a call to the dance floor and the sound of humanity locked in an ominously downward spiral. But theres plenty of optimism and positive energy too, running from the yearning trilingual pop of Dsol to the certain assertion that We could do so much better than this on Momentary Bliss. Strange isnt bad, its just weird, says Kabaka. Were intelligent enough to recognize that mutation changes how the world develops, and change is good. In the 20 years between this album and Gorillazs first EP, <i>Tomorrow Comes Today</i>, the world has mutated in unimaginable ways. Gorillaz, though, remain a vibrant and inventive bulletin of their times. The essence [of Gorillaz] is the same as its always been, says Kabaka. Its still weird and its still wonderful. But maybe we have changed.",
            "short": "The essence of Gorillaz is the same as always. Its still weird, still wonderful."
          },
          "isComplete": true
        }
      },
